<template>
  <div class="main-wrapper" @click="setToDoAdding(false)">
    <div class="menu" :class="{ closed: showMenu === false }">
      <div class="menu-opener">
        <form-button @click="showMenu = !showMenu">
          <font-awesome-icon
            class="fa-xl"
            :class="{ right: showMenu !== false, left: showMenu === false }"
            icon="fa-solid fa-arrow-right"
          />
        </form-button>
      </div>
      <div class="menu__user">
        <img class="menu__user_logo" src="@/images/CatXD.png" />
        <h3 class="menu__user_name">AlexeiLazarevVue</h3>
        <p class="menu__user_job">Vue-developer</p>
      </div>
      <div class="menu__content">
        <router-link
          v-for="desk in getDesks()"
          :key="desk.id"
          v-bind:to="`/${desk.id}`"
          class="menu__content_link"
        >
          {{ desk.name }}
          <router-link v-bind:to="`/`">
            <form-button class="delete" @click="removeDesk(desk.id)">
              <font-awesome-icon class="icon" icon="fa-solid fa-trash" />
            </form-button>
          </router-link>
        </router-link>
      </div>

      <to-do-form @create="createDesk"></to-do-form>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import ToDoForm from "@/components/ToDoForm";
import { mapGetters, mapMutations } from "vuex";
export default {
  data() {
    return {
      showMenu: false,
    };
  },
  components: { ToDoForm },
  methods: {
    ...mapGetters({ getDesks: "deskStoreModule/getDesks" }),
    ...mapMutations({
      addDesk: "deskStoreModule/addDesk",
      removeDesk: "deskStoreModule/removeDesk",
      setToDoAdding: "inputsController/setToDoAdding",
    }),
    createDesk(newDesk) {
      newDesk.name = newDesk.value;
      this.addDesk(newDesk);
    },
  },
};
</script>

<style lang="less">
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat";
  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
    background-color: linear-gradient(135deg, #0065ef, #006ca6);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #0065ef;
    border-radius: 20px;
  }
}
@keyframes rotateAnimation {
  100% {
    transform: rotate(540deg);
  }
}

@keyframes rotateAnimations {
  0% {
    transform: rotate(540deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.main-wrapper {
  display: flex;
  flex-direction: row;
  height: 100vh;
}
body {
  background: #ebebf4;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.4px;
  color: #343756;
}
.closed {
  transform: translateX(-250px);
}
.menu {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100%;
  width: 300px;
  background: #cfdddb;
  overflow-y: auto;
  -webkit-box-shadow: 5px 5px 5px 5px rgba(34, 60, 80, 0.2);
  transition: 0.5s;
  transition-delay: 0.4s;
  &-opener {
    background: none;
    display: flex;
    justify-content: flex-end;
    button {
      background: none !important;
      position: relative;
      &:hover {
      }
      .right {
        animation: rotateAnimation 1s cubic-bezier(.83,-0.69,0,1.62)
          forwards;
      }
      .left {
        animation: rotateAnimations 1s cubic-bezier(.83,-0.69,0,1.62)
          forwards;
      }
    }
  }
  form {
    input {
      padding: 20px !important;
      border-radius: 0 !important;
    }
    button {
      border-radius: 0 !important;
    }
  }

  &__user {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 200px;
    align-items: center;
    justify-content: center;
    &_logo {
      height: 100px;
      width: 100px;
      margin-bottom: 10px;
      border-radius: 100%;
      cursor: pointer;
    }
  }
  &__content {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    &_link {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #616969;
      text-decoration: none;
      width: 100%;
      word-break: break-all;
      padding: 10px 40px;
      transition: 0.1s;
      a {
        position: absolute;
        right: 15px;
      }
      &:hover {
        cursor: pointer;
        color: #f2f2f8;
        background: rgba(83, 85, 223, 0.85);
        .delete {
          opacity: 1;
        }
      }
      .delete {
        padding: 0;
        height: 100%;
        background: none;
        opacity: 0;
        transition: 0.3s;
        &:hover {
          color: #f2f2f8;
        }
      }
    }
  }
}
</style>